<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
      <el-form-item prop="materialGroupCode">
        <el-select
          v-model="query.materialGroupCode"
          filterable
          remote
          placeholder=""
          :remote-method="remoteMethod"
          :loading="selectLoading"
          style="width: 178px;"
          clearable
          @clear="clearInfo"
        >
          <el-option
            v-for="item in options"
            :key="item.code"
            :label="item.code"
            :value="item.code"
          />
        </el-select>
      </el-form-item>
      <el-form-item>
        <!--  -->
        <el-button type="primary" icon="el-icon-search" @click="searchInit" />
        <!--  -->
        <el-button icon="el-icon-refresh" @click="resetForm('filterItem')" />
        <!--  -->
        <el-button v-if="mode === 'multiple'" type="primary" icon="el-icon-check" :disabled="!selected.length" @click="handleMultipleConfirm">{{ !selected.length ? '' : ` (${selected.length})` }}</el-button>
        <!--  -->
        <el-button v-if="mode === 'single'" type="primary" icon="el-icon-check" :disabled="!radio" @click="handleSingleConfirm" />
      </el-form-item>
    </el-form>
    <el-scrollbar class="query-more-scroll-drawer">
      <el-row>
        <el-table ref="table" v-loading="loading" :data="tableData" border stripe style="width: 100%" row-key="id" @selection-change="handleSelectionChange">
          <!--  -->
          <el-table-column :align="'center'" type="index" :index="indexMethod" label="" />
          <!--  -->
          <el-table-column v-if="mode === 'multiple'" type="selection" width="55" :align="'center'" reserve-selection :selectable="handleSelectable" />
          <!--  -->
          <el-table-column v-if="mode === 'single'" :align="'center'" #default="{row}" width="55">
            <el-radio v-model="radio" :disabled="selectedCodes.includes(row.id)" :label="row.id" />
          </el-table-column>
          <el-table-column :align="'center'" prop="code" label="" show-overflow-tooltip width="190" />
          <el-table-column :align="'center'" prop="description" label="" show-overflow-tooltip min-width="190" />
          <el-table-column :align="'center'" prop="dataSources_dictText" label="" show-overflow-tooltip />
        </el-table>
      </el-row>
      <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    </el-scrollbar>
  </el-dialog>
</template>

<script>
import { getListApi, getPullListItemApi } from '@/apis/modules/base/baseItemGroup'
import paginationPage from '@/mixins/paginationPage'
import Pagination from '@/components/Pagination'
import { requestWrapper } from '@/utils/utils'

const INIT_FORM = {
  materialGroupCode: ''
}

export default {
  components: {
    Pagination
  },
  mixins: [
    paginationPage
  ],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // 
    extraInfo: {
      type: Object,
      default: () => {}
    },
    // 
    selectedCodes: {
      type: Array,
      default: () => []
    },
    // 
    mode: {
      type: String,
      default: 'multiple' // single
    }
  },
  data() {
    return {
      loading: false,
      query: { ...INIT_FORM },
      tableData: [],
      selected: [],
      radio: '',
      selectLoading: false, // loading
      options: [] // 
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    async init() {
      this.loading = true
      const param = {
        page: {
          ...this.pageInfo
        },
        ...this.query
      }
      const res = await requestWrapper(getListApi(param))
      this.loading = false
      if (!res.success) return
      const { list, ...pageInfo } = res.data
      this.initPage(pageInfo)
      this.tableData = list
    },
    // 
    initSelect() {
      this.searchInit()
      this.mode === 'single' && (this.radio = '')
      this.mode === 'multiple' && this.$refs.table.clearSelection()
    },
    // 
    async remoteMethod(query) {
      this.options = []
      if (query !== '') {
        this.selectLoading = true
        // Tip 
        const res = await requestWrapper(getPullListItemApi({
          materialGroupCode: query
        }))
        if (!res.success) return
        this.selectLoading = false
        this.options = res.data
      } else {
        this.options = []
      }
    },
    clearInfo() {
      this.options = []
    },
    // 
    handleSelectionChange(val) {
      this.selected = val
    },
    // 
    handleSelectable(row, index) {
      return !this.selectedCodes.includes(row.id)
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 
    beforeClose() {
      this.resetForm('filterItem')
      this.$emit('input', false)
    },
    // 
    handleMultipleConfirm() {
      this.$emit('confirm', this.selected)
      this.beforeClose()
    },
    // 
    handleSingleConfirm() {
      this.$emit('confirm', [
        this.tableData.find(item => item.id === this.radio)
      ])
      this.beforeClose()
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-radio__label{
  display: none;
}
.query-more-scroll-drawer {
  height: 600px;
}
</style>

